import React from 'react';

const Text = (props) => {
	const date = new Date(Number(props.match.params.date));
	return (
		<div className="text block">
			<h1 className="title">Текстовая страница</h1>
			<div className="text__text">
				Это простая текстовая страница для отображения параметров роута. Параметры:<br/>- id: {props.match.params.id} <br/>- date: {`${date.getMonth()}/${date.getDate()}/${date.getFullYear()} ${date.getHours()}:${date.getMinutes()}`}
			</div>
		</div>
	)
}

export default Text;